<template>
  <v-card min-height="80vh" max-height="80vh" style="overflow-y:auto" class="mx-auto">
    <v-col class="mx-auto" cols="12">
      <grid-products @reloadData="getCategories()" :productsArray="categoryProducts" :titleCard="`${this.$route.query.name}`"></grid-products>
    </v-col>
  </v-card>
</template>

<script>
import GridProducts from "@/components/Products/GridProducts";
import { mapState, mapActions } from "vuex";
export default {
  components: {
    gridProducts: GridProducts
  },
  computed: {
    ...mapState("products/stores", ["categoryProducts"])
  },
  data() {
    return {};
  },
  mounted() {
    this.getCategories();
  },
  methods: {
    ...mapActions("products/stores", ["getProductsByCategory"]),
    getCategories() {
      if (this.$route.params.id && this.$route.query.cat) {
        const data = {
          id_store: this.$route.params.id,
          id_category: this.$route.query.cat
        };
        this.getProductsByCategory(data);
      }
    }
  },
  props: {
    idCategory: Number
  },
  watch: {
    $route(to, from) {
      this.getCategories();
    }
  }
};
</script>

